@import url(https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed:wght@600;700&display=swap);
body {
  margin: 0;
  font-family: 'Barlow Semi Condensed';
}

html {
  background: radial-gradient(hsl(214, 47%, 23%), hsl(237, 49%, 15%));
  background-repeat: no-repeat;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
    max-width: 1336px;
    margin: 0 auto;
}
.battle-container {
    display: flex;
    justify-content: space-evenly;
    margin-top: 150px;
}

.user-picked, .adversary-picked {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.battle-container h2 {
    color: #fff;
}

.battle-container div .option {
    margin-top: 20px;
}

.option {
    background: #fff;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    
}

.option.option-waiting {
    width: 100px;
    height: 100px;
    background:  rgba(0,0,0, .15)
}

.option:hover {
    cursor: pointer;
}

.winner {
    width: 160px;
    height: 160%;
    position: absolute;
    background: rgba(255, 255, 255, .08);
    z-index: -200;
    border-radius: 50%;
}

.winner::after {
    content: '';
    width: 190px;
    height: 190px;
    position: absolute;
    background: rgba(255, 255, 255, .05);
    left: -15px;
    top: -15px;
    border-radius: 50%;
}

.winner::before {
    content: '';
    width: 220px;
    height: 220px;
    position: absolute;
    background: rgba(255, 255, 255, .02);
    left: -30px;
    top: -30px;
    border-radius: 50%;
}

.option .paper::after {
    content: '';
    background: radial-gradient(hsl(230, 89%, 62%), hsl(230, 89%, 65%));
    width: 125px;
    height: 125px;
    position: absolute;
    top: -13%;
    left: -13%;
    z-index: -100;
    border-radius: 50%;
}

.option .rock::after {
    content: '';
    background: radial-gradient(hsl(349, 71%, 52%), hsl(349, 70%, 56%));
    width: 125px;
    height: 125px;
    position: absolute;
    top: -13%;
    left: -13%;
    z-index: -100;
    border-radius: 50%;
}

.option .scissors::after {
    content: '';
    background: radial-gradient(hsl(39, 89%, 49%), hsl(40, 84%, 53%));
    width: 125px;
    height: 125px;
    position: absolute;
    top: -13%;
    left: -13%;
    z-index: -100;
    border-radius: 50%;
}

.option .lizard::after {
    content: '';
    background: radial-gradient(hsl(261, 73%, 60%), hsl(261, 72%, 63%));
    width: 125px;
    height: 125px;
    position: absolute;
    top: -13%;
    left: -13%;
    z-index: -100;
    border-radius: 50%;
}

.option .spock::after {
    content: '';
    background: radial-gradient(hsl(189, 59%, 53%), hsl(189, 58%, 57%));
    width: 125px;
    height: 125px;
    position: absolute;
    top: -13%;
    left: -13%;
    z-index: -100;
    border-radius: 50%;
}

.winner-section {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.winner-section button {
    background: rgb(255, 255, 255);
    color: rgb(220, 0, 0);
    border: none;
    padding: 10px 30px;
    font-weight: bold;
    border-radius: 10px;
}
.menu-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: radial-gradient(hsl(214, 47%, 23%), hsl(237, 49%, 15%))
}

.menu-container h2 {
    color: #fff;
    text-align: center;
}

.menu-container .container {
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.menu-container .container > div {
    width: 100%;
}

.menu-container .container .buttons {
    display: flex;
    justify-content: space-evenly;
    margin-top: 30px;
}

.buttons button {
    background: transparent;
    border: none;
    color: #fff;
    border: 1px solid rgb(218, 221, 39);
    padding: 10px 30px;
    width: 25%;
}

.buttons button:hover {
    cursor: pointer;
}
.rules-container {
    position: absolute;
    bottom: 30px;
    right: 40px;
}

.rules-container button {
    background: transparent;
    border: none;
    border: 1px solid #fff;
    padding: 15px 40px;
    color: #fff;
    border-radius: 10px;
}

.rules-container button:hover {
    cursor: pointer;
}
.rulesModal-container {
    background: #fff;
    width: 400px;
    height: 400px;
    margin: auto;
    position: absolute;
    top: 0; left: 0; bottom: 0; right: 0;
    border-radius: 10px;
}

.rulesModal-container div:first-child {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0px 20px;
}

.rulesModal-container button {
    background: transparent;
    border: none;
}

.rulesModal-container h2 {
    color: hsl(229, 25%, 31%);
}

.rulesModal-container button:hover {
    cursor: pointer;
}


.rulesModal-container div:last-child {
    display: flex;
    justify-content: center;
}
.scoreBoard {
    margin-top: 50px;
}

.scoreBoard > div {
    width: 60%;
    border-radius: 10px;
    margin: 0 auto;
    border: .1px solid #fff;
    display: flex;
    justify-content: space-between;
    padding: 20px;
}

.score {
    background-color: #fff;
    border-radius: 5px;
    width: 100px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
}

.score h2 {
    color: hsl(229, 64%, 46%);
    font-size: .7em;
    margin: 0;
}

.score p {
    color: hsl(217, 16%, 45%);
    font-weight: bold;
    font-size: 2.3em;
    margin: 0;
}
.default-container, .bonus-container {
    display: flex;
    justify-content: center;
    margin-top: 80px;
}

.default-container > div, .bonus-container > div {
    position: relative;
}

.option {
    background: #fff;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.option:hover {
    cursor: pointer;
}

.default-container .option.paper {
    position: absolute;
    left: -30px;
    top: -30px;
}

.default-container .option.paper::after,
.bonus-container .option.paper::after {
    content: '';
    background: radial-gradient(hsl(230, 89%, 62%), hsl(230, 89%, 65%));
    width: 125px;
    height: 125px;
    position: absolute;
    top: -13%;
    left: -13%;
    z-index: -100;
    border-radius: 50%;
}

.default-container .option.rock {
    position: absolute;
    right: -30px;
    top: -30px;
}

.default-container .option.rock::after,
.bonus-container .option.rock::after
{
    content: '';
    background: radial-gradient(hsl(349, 71%, 52%), hsl(349, 70%, 56%));
    width: 125px;
    height: 125px;
    position: absolute;
    top: -13%;
    left: -13%;
    z-index: -100;
    border-radius: 50%;
}

.default-container .option.scissors {
    position: absolute;
    bottom: -10px;
    left: 100px;
}

.default-container .option.scissors::after,
.bonus-container .option.scissors::after {
    content: '';
    background: radial-gradient(hsl(39, 89%, 49%), hsl(40, 84%, 53%));
    width: 125px;
    height: 125px;
    position: absolute;
    top: -13%;
    left: -13%;
    z-index: -100;
    border-radius: 50%;
}

.bonus-container .option.lizard::after {
    content: '';
    background: radial-gradient(hsl(261, 73%, 60%), hsl(261, 72%, 63%));
    width: 125px;
    height: 125px;
    position: absolute;
    top: -13%;
    left: -13%;
    z-index: -100;
    border-radius: 50%;
}

.bonus-container .option.spock::after {
    content: '';
    background: radial-gradient(hsl(189, 59%, 53%), hsl(189, 58%, 57%));
    width: 125px;
    height: 125px;
    position: absolute;
    top: -13%;
    left: -13%;
    z-index: -100;
    border-radius: 50%;
}

.bonus-container .option.paper {
    position: absolute;
    right: -35px;
    top: 75px;
}

.bonus-container .option.scissors {
    position: absolute;
    top: -50px;
    right: 120px;
}

.bonus-container .option.rock {
    position: absolute;
    right: 30px;
    bottom: -50px;
}

.bonus-container .option.spock {
    position: absolute;
    left: -35px;
    top: 75px;
}

.bonus-container .option.lizard {
    position: absolute;
    left: 30px;
    bottom: -50px;
}
